.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  .header {
    height: 60px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
    padding: 0 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .title {
      font-size: 30px;
      font-weight: 600;
    }
  }

  .calendar {
    padding: 16px;
    flex: 1;
    display: flex;
    flex-direction: column;
    .head,
    .body {
      list-style: none;
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      text-align: center;
    }
    .head {
      font-size: 16px;
      height: 9%;
      justify-content: space-between;
      align-items: center;
      color: rgba(0,0,0,.65);
      .item {
        width: calc(100%/7);
      }
    }
    .body {
      flex: 1;
      display: grid;
      grid-template-columns: repeat(7,14.285%);
      .item {
        cursor: pointer;
        padding: 4px;
      }
    }

    .inner {
      width: 100%;
      height: 100%;
      border-radius: 6px;
      border: 2px solid transparent;
      display: flex;
      flex-flow: wrap;
      align-items: center;
      justify-content: center;
      position: relative;
      box-sizing: border-box;
      .info {
        b {
          display: block;
          font-weight: normal;
          font-size: 24px;
          margin-bottom: 3px;
          color: #333;
          font-weight: 600;
        }

        i {
          display: block;
          font-style: normal;
          color: rgba(30,31,36,.5);
          font-size: 12px;
        }
      }
      &:hover {
        border-color: rgba(235,51,51,.15);
      }
      &.other {
        opacity: .3;
      }
      &.festival .info i {
        color: var(--app-theme-color);
      }
      &.weekend .info b {
        color: var(--app-theme-color);
      }
      &.today {
        border-color: var(--app-theme-color);
      }
      &.selected {
        background-color: var(--app-theme-color);
        .info b, .info i {
          color: #fff;
        }
        .tags span {
          &.isToday, &.isRest, &.isHoliday {
            color: var(--app-theme-color);
            background-color: #fff;
          }
        }
      }
      .tags {
        position: absolute;
        top: 2px;
        right: 2px;
        display: flex;
        margin: 0 -1px;
        span {
          display: block;
          font-size: 12px;
          color: #fff;
          background-color: #fff;
          width: 16px;
          height: 16px;
          border-radius: 4px;
          margin: 0 1px;
          &.isToday {
            background-color:  var(--app-theme-color);
          }
          &.isRest {
            background-color: #4e5877;
          }
          &.isHoliday {
            background-color:  var(--app-theme-color);
          }
        }
      }
    }
  }
}
