.container {
  padding: 30px 16px;
  .wrap {
    height: 100%;
    overflow-y: auto;
    .details {
      padding: 10px 0;
      text-align: center;
      p {
        font-size: 14px;
        line-height: 20px;
        color: rgba(0,0,0,0.85);
        font-weight: 300;
        &.detail_day {
          width: 80px;
          height: 80px;
          line-height: 90px;
          background-color: var(--app-theme-color);
          position: relative;
          border-radius: 10px;
          color: #fff;
          font-size: 50px;
          margin: 6px 0;
          text-align: center;
          display: inline-block;
          &:after, &:before {
            content: '';
            display: block;
            position: absolute;
            height: 6px;
            width: 6px;
            background-color: #fff;
            border-radius: 50%;
            top: 10px;
          }
          &:before {
            left: 10px;
          }
          &:after {
            right: 10px;
          }
        }
      }
    }
    .info {
      p {
        border-top: 1px solid rgba(0,0,0,0.05);
        color: #939393;
        font-size: 13px;
        line-height: 20px;
        font-weight: 300;
        display: flex;
        div {
          display: flex;
          flex-wrap: wrap;
          padding: 5px 0;
          flex: 1;
        }
        span {
          flex: 1;
        }
        .tag {
          border-radius: 4px;
          height: 20px;
          line-height: 20px;
          font-size: 12px;
          text-align: center;
          background: var(--app-theme-color);
          margin-right: 10px;
          color: #fff;
          padding: 0 4px;
          flex: initial;
        }
      }
      .huangli {
        margin-top: 12px;
        border-radius: 2px;
        background-color: #fff;
        :first-child {
          border-top: none;
        }
        p > div {
          flex-direction: column;
          align-content: center;
          align-items: center;
          font-size: 12px;
          .tag {
            margin: 0px;
            color: #c69c70;
            background-color: transparent;
            height: initial;
          }
          span  {
            padding: 0 1px;
          }
          i {
            font-style: normal;
          }
        }
        .shenqu {
          flex: 1;
          text-align: center;
          span {
            padding: 0 3px;
          }
          .tag {
            padding: 0 6px;
          }
        }
        .shichenyiji {
          flex-direction: column;
          .shichen {
            flex-direction: initial;
            text-align: center;
            line-height: normal;
            padding-top: 0;
          }
        }
      }
    }
  }
}
