.layout {
  display: flex;
  width: 100%;
  flex-direction: row-reverse;
  .left {
    width: 300px;
    flex-shrink: 0;
    height: 100%;
    border-right: 1px solid rgba(0, 0, 0, 0.08);
    background-color: rgb(248, 248, 248);
  }

  .right {
    width: 100%;
    height: 100%;
    flex: 1;
  }
}
